<!--  -->
<template>
  <div class="goods">
    <!-- 1. -->
    <div class="header">
      <!-- change		(value: string | number) -->
      <el-input
        @change="searchInput"
        v-model="searchInfo.searchKey"
        placeholder=""
      ></el-input>
      <el-button type="primary" @click="searchInput"></el-button>
      <el-button type="primary">
        <router-link to="/add-goods" style="color: #fff"></router-link>
      </el-button>
      <el-button type="primary" @click="addGoods"></el-button>
    </div>
    <!-- 2. -->
    <div class="wrapper">
      <el-table :data="tableData" border>
        <el-table-column type="selection" width="50"></el-table-column>
        <el-table-column prop="id" label="ID" width="170"></el-table-column>
        <el-table-column
          prop="name"
          label=""
          width="120"
          show-overflow-tooltip
        ></el-table-column>
        <el-table-column
          prop="price"
          label=""
          width="80"
        ></el-table-column>
        <el-table-column
          prop="num"
          label=""
          width="80"
        ></el-table-column>
        <el-table-column
          prop="category"
          label=""
          width="80"
          show-overflow-tooltip
        ></el-table-column>
        <el-table-column
          prop="image"
          label=""
          show-overflow-tooltip
        ></el-table-column>
        <el-table-column
          prop="date"
          label=""
          width="120"
        ></el-table-column>
        <el-table-column
          prop="text"
          label=""
          show-overflow-tooltip
        ></el-table-column>
        <el-table-column prop="operation" label="" width="180">
          <template slot-scope="scope">
            <el-button size="mini" @click="handleEdit(scope.$index, scope.row)"
              >
            </el-button>
            <el-button
              size="mini"
              type="danger"
              @click="handleDelete(scope.$index, scope.row)"
              >
            </el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <!-- 3. -->
    <div class="laypage">
      <Pagination
        :total="total"
        :pageSize="pageSize"
        @changePage="changePage"
      ></Pagination>
    </div>
    <!-- 4. -->
    <!-- : 1.props $emit 2.children 3.ref-->
    <!-- <GoodsDialog :dialogVisible="dialogVisible" @closeDialog="closeDialog"></GoodsDialog> -->
    <GoodsDialog
      :dialogTitle="dialogTitle"
      :rowData="rowData"
      ref="dialog"
      @submitToGoods="submitToGoods"
      @editToGoods="editToGoods"
    ></GoodsDialog>
  </div>
</template>

<script>
// import { getList } from "../../../api/getData";
import axios from "axios";
import Pagination from "../../components/Pagination.vue";
import GoodsDialog from "./GoodsDialog.vue";
// import moment from "moment";
export default {
  data() {
    return {
      rowData: {}, //
      dialogTitle: "",
      // dialogVisible:false,
      tableData: [
        // {
        //   date: "2016-05-02",
        //   name: "",
        //   address: " 1518 ",
        // },
        // {
        //   date: "2016-05-04",
        //   name: "",
        //   address: " 1517 ",
        // },
        // {
        //   date: "2016-05-01",
        //   name: "",
        //   address: " 1519 ",
        // },
        // {
        //   date: "2016-05-03",
        //   name: "",
        //   address: " 1516 ",
        // },
      ],
      pageSize: 0, //
      total: 0, //
      searchInfo: { searchKey: "" }, //
    };
  },
  components: { Pagination, GoodsDialog },

  methods: {
    editToGoods(newTableData) {
      // console.log('');
      console.log("newTableData", newTableData);
      this.tableData.forEach((item) => {
        if (item.id === newTableData.id) {
          item.name = newTableData.name;
          item.price = newTableData.price;
          item.num = newTableData.num;
          item.date = newTableData.date;
          item.image = newTableData.image;
          item.text = newTableData.text;
          item.category = newTableData.category;
        }
      });
    },
    submitToGoods(newTableData) {
      // console.log('');
      // newTableData.date=moment(newTableData.date).format("YYYY-MM-DD")
      console.log("newTableData", newTableData);
      this.tableData.unshift(newTableData);
    },
    addGoods() {
      //  this.dialogVisible=true
      //----------------------
      //
      this.$refs.dialog.dialogVisible = true;
      this.dialogTitle = "";
      // this.rowData={}
    },
    // closeDialog(){
    //   this.dialogVisible=false
    // },
    searchInput(val) {
      // console.log("", val);
      this.getMockList(0);
    },
    changePage(page) {
      // debugger
      this.getMockList(page - 1);
    },
    handleEdit(index, row) {
      console.log("index", index, "row", row);
      this.$refs.dialog.dialogVisible = true;
      this.dialogTitle = "";
      //
      // this.$refs.dialog.goodsForm = row;
      // this.rowData=row  //row={}  
      this.rowData = { ...row };
    },
    handleDelete(index, row) {
      console.log("index", index, "row", row);
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      })
        .then(() => {
          // 
          this.tableData = this.tableData.filter((item) => {
            return item.id !== row.id;
          });
          this.$message({
            type: "success",
            message: "!",
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "",
          });
        });
    },

    async getMockList(num) {
      //
      try {
        const url = "user/goodsList";
        // console.log(this);
        const res = await axios.post(url, {
          params: {
            pageIndex: num, //
            pageSize: 10,
            searchKey: this.searchInfo.searchKey.trim(),
          },
        });
        // console.log("res.data", res.data);
        this.tableData = res.data.data.rows;
        this.total = res.data.data.total;
        this.pageSize = res.data.data.pageSize;
      } catch (err) {
        console.dir(err);
        this.$message.error({
          message: err.message,
          duration: 1500,
        });
      }
    },
  },
  mounted() {
    this.getMockList(0);
  },
};
</script>


<style scoped>
.goods {
  margin: 20px;
}
.header {
  display: flex;
}
.header button {
  margin-left: 10px;
}
.wrapper {
  margin-top: 20px;
}
</style>
