<!--  -->
<template>
  <div>
    <el-dialog
      :title="dialogTitle"
      :visible.sync="dialogVisible"
      width="80%"
      :before-close="handleClose"
    >
      <div class="my-form">
        <el-form
          :model="goodsForm"
          :rules="rules"
          ref="goodsForm"
          label-width="100px"
          class="demo-ruleForm"
        >
          <el-form-item label="" prop="category">
            <el-button type="primary" @click="innerVisible = true"
              ></el-button
            >
            <span>{{ goodsForm.category }}</span>
          </el-form-item>
          <el-form-item label="" prop="name">
            <el-input v-model="goodsForm.name"></el-input>
          </el-form-item>
          <el-form-item label="" prop="price">
            <el-input v-model="goodsForm.price"></el-input>
          </el-form-item>
          <el-form-item label="" prop="num">
            <el-input v-model="goodsForm.num"></el-input>
          </el-form-item>
          <el-form-item label="" prop="image">
            <el-button type="primary" @click="innerVisibleImg = true"
              ></el-button
            >
            <span>{{ goodsForm.image }}</span>
          </el-form-item>
          <el-form-item label="">
            <el-col :span="11">
              <el-form-item prop="date">
                <el-date-picker
                  type="date"
                  placeholder=""
                  v-model="goodsForm.date"
                  style="width: 100%"
                  format="yyyy  MM  dd "
                  value-format="yyyy-MM-dd"
                ></el-date-picker>
              </el-form-item>
            </el-col>
          </el-form-item>

          <el-form-item label="" prop="text">
            <!-- <el-input type="textarea" rows="1" v-model="goodsForm.text" placeholder=""></el-input> -->
            <WangEditor
              @sendGoodsText="sendGoodsText"
              ref="myEditor"
            ></WangEditor>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="resetForm('goodsForm')"
              ></el-button
            >
          </el-form-item>
        </el-form>
      </div>

      <span slot="footer" class="dialog-footer">
        <el-button @click="closeForm('goodsForm')"></el-button>
        <el-button type="primary" @click="submitForm('goodsForm')"
          ></el-button
        >
      </span>
      <!--  -->
      <el-dialog
        width="40%"
        title=""
        :visible.sync="innerVisible"
        append-to-body
      >
        <CategorySelect @sendTreeData="sendTreeData"></CategorySelect>
        <span slot="footer" class="dialog-footer">
          <el-button @click="innerVisible = false"></el-button>
          <el-button type="primary" @click="showTreeData"></el-button>
        </span>
      </el-dialog>

      <!--  -->
      <el-dialog
        width="40%"
        title=""
        :visible.sync="innerVisibleImg"
        append-to-body
      >
        <UploadImg @sendImg="sendImg"></UploadImg>
        <span slot="footer" class="dialog-footer">
          <el-button @click="innerVisibleImg = false"></el-button>
          <el-button type="primary" @click="showImg"></el-button>
        </span>
      </el-dialog>
    </el-dialog>
  </div>
</template>

<script>
import CategorySelect from "./CategorySelect.vue";
import UploadImg from "./UploadImg.vue";
import WangEditor from "./WangEditor.vue";
import { nanoid } from "nanoid";
export default {
  components: { CategorySelect, UploadImg, WangEditor },
  name: "GoodsDialog",
  // props: ["dialogVisible"],
  // props: ["dialogTitle", "rowData"],
  props: {
    dialogTitle: {
      type: String,
      default: "",
    },
    rowData: {
      type: Object,
      default: () => {},
    },
  },
  data() {
    return {
      treeData: {},
      innerVisibleImg: false, //
      innerVisible: false, //
      dialogVisible: false, //
      goodsForm: {
        name: "",
        id: "",
        price: "",
        num: "",
        date: "",
        image: "",
        text: "",
        category: "",
      },
      rules: {
        name: [
          { required: true, message: "", trigger: "blur" },
          // { min: 3, max: 5, message: " 3  5 ", trigger: "blur" },
        ],
        text: [{ required: false, message: "", trigger: "blur" }],
      },
    };
  },

  watch: {
    rowData(val) {
      console.log("", val);
      this.goodsForm = val;
      // console.log('---this.$refs.myEditor',this.$refs.myEditor);
      // this.$refs.myEditor.html=val.text
      // 
      this.$nextTick(() => {
        console.log("---this.$refs.myEditor", this.$refs.myEditor);
        this.$refs.myEditor.html = val.text;
      });
    },
  },
  methods: {
    sendGoodsText(html) {
      // console.log('html',html);
      this.goodsForm.text = html;
    },
    showImg() {
      this.innerVisibleImg = false;
    },
    sendImg(img) {
      console.log("", img);
      this.goodsForm.image = img;
    },
    showTreeData() {
      this.innerVisible = false;
      this.goodsForm.category = this.treeData.name;
    },
    sendTreeData(data) {
      console.log("sendTreeData", data);
      this.treeData = data;
    },
    // closeDialog() {
    //   // this.$emit("closeDialog");
    //   //-----------------------
    //   this.dialogVisible = false;
    // },
    handleClose(done) {
      this.$confirm("")
        .then((_) => {
          // done();
          this.closeForm("goodsForm");
        })
        .catch((_) => {});
      //refdialogVisible
    },
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          console.log("this.goodsForm", this.goodsForm);
          //  
          if (this.dialogTitle === "") {
            this.goodsForm.id = nanoid();
            console.log("");
            this.$emit("submitToGoods", this.goodsForm);
            // alert("submit!");
            this.$message({
              message: "",
              type: "success",
            });
          } else {
            console.log("");
            this.$emit("editToGoods", this.goodsForm);
            this.$message({
              message: "",
              type: "success",
            });
          }
          this.closeForm(formName);
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    closeForm(formName) {
      this.dialogVisible = false;
      this.resetForm("goodsForm");
    },
    resetForm(formName) {
      //this.goodsForm.id = "";
      // this.$refs[formName].resetFields();//
      //  console.log('this.$refs', this.$refs);
      this.goodsForm = {
        name: "",
        id: "",
        price: "",
        num: "",
        date: "",
        image: "",
        text: "",
        category: "",
      };
      this.$refs.myEditor.editor.clear();
    },
  },
};
</script>

<style scoped>
.my-form {
  background-color: #fff;
  padding: 10px 30px 10px 10px;
}
</style>
